.app {
  display: grid;
  grid-template-areas:
    "header header"
    "sidebar main";
  grid-template-columns: 1fr 3fr;
  column-gap: 20px;
  row-gap: 50px;
}

.header {
  grid-area: header;
  width: 100%;
}

.sidebar {
  grid-area: sidebar;
  height: fit-content;
}

.main {
  grid-area: main;
}