.button {
  padding: 20px 40px;
  width: 100%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 5px;
  text-align: center;

  border-radius: 5px;
  background: #da5902;
  color: #ffffff;

  font-size: 20px;
  font-weight: 600;
}